export default {
	data() {
		return {
			title: 'Hello',
			data: [{name:"我的资料",Lick_url:""},
			{name:"我的任务",Lick_url:""},
			{name:"我的收藏",Lick_url:""},
			{name:"问题反馈",Lick_url:""},
			{name:"关于公司",Lick_url:""},
			{name:"检查更新",Lick_url:"./mine/Update"}
			],
			timg: require('../../static/imags/download.jpg'),
			username: '', //登录用户
			
		}
	},
	onShow() {
		var that = this
		uni.getStorage({
			key: 'Token',
			success: function(res) {
				that.username = JSON.parse(res.data).FullName
			}
		});
	},
	methods: {
		Click_Index:function(item){
	
		},

		//退出登录
		botLogo: function() {
			uni.navigateTo({
				url: "../Logo/Logo",
			});
		},
		botLogos: function() {
			var that = this
			this.$store.state.token = ''
			uni.setStorage({ //清空本地Token
				key: 'Token',
				data: '',
				success: function() {
					uni.setStorage({ //
						key: 'admin',
						data: '',
						success: function() {
							if(that.username == ''){
								uni.navigateTo({
									url: "../Logo/Logo",
								});
							}else{
								uni.setStorage({ //储存登录名
									key: 'admins',
									data: that.username,
									success: function() {
										that.$store.state.username = ''
										uni.navigateTo({
											url: "../Logo/Logo",
										});
									}
								});
							}
						}
					});
				}
			});
		}
	}
}
